body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  min-height: 100vh;
  font-family: 'Arvo', serif;
}

.leftBorder {
  width: 75px;
  background-color: #1D1E20;
}

.pageContainer {
  background-color: #111111;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 60px 100px 60px 45px;
}
.mainContent p {
    color: #FFFFFF;
    opacity: 0.3 !important;
    position: absolute;
    left: 30px;
    font-size: 18px !important;
}

.mainContent {
    color: white;
    font-size: 40px;
}

.name {
    background: linear-gradient(127.76deg, #0DFC9C 0%, #00BDC0 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.mainContent a {
    color: #FFFFFF;
    opacity: 0.5;
    margin: 0 10px;
    cursor: pointer;
    text-decoration: none !important;
}

.indent {
    padding-left: 60px;
    font-size: 30px !important;
}

.line3 {
    color: #FFFFFF;
}

.mainPic {
    border-radius: 50%;
    width: 130px;
    padding: 25px 20px;
}
.workTile {
    height: 450px;
    display: flex;
    justify-content: space-around;
    margin: 160px 0px;
}

.workTile .leftContent {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 60px 0px;
    width: 30%;
}

.rightContent {
    position: relative;
    margin-right: 250px;
}

.superTitle {
    font-size: 20px;
}

.title {
    width: 50%;
    font-size: 60px;
}

.built {
    font-size: 12px;
    font-weight: bold;
    opacity: 0.4;
    font-family: 'Roboto', sans-serif !important;
}

.techSection {
    display: flex;
}

.tech {
    margin-right: 20px;
    font-size: 12px;
    font-weight: bold;
    font-family: 'Roboto', sans-serif !important;
}

.box {
    width: 450px;
    height: 450px;
    /*background-color: #D8D8D8;*/
}

.button {
    position: absolute;
    bottom: 65px;
    left: -150px;
    width: 200px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(127.76deg, #0DFC9C 0%, #00BDC0 100%);
}

.rightContent a {
    font-size: 18px;
    text-decoration: none;
    color: #FFFFFF
}

.videoFrame {
    max-width: 800px !important;
    width: 50vw !important;
}

.videoFrameMobile {
    max-width: 250px !important;
    width: 50vw !important;
}

.workPage {
    color: #FFFFFF;
    height: 100%;
}

.header {
    margin-top: 30px;
    margin-bottom: 130px;
    font-size: 30px;
}

.footer {
    font-size: 30px;
}
.aboutPage {
    height: 90%;
    color: #FFFFFF;
    display: flex;
    justify-content: space-around;
    margin: 0px 150px;
}

.aboutPage .mainPic {
    width: 400px !important;
}

.aboutPage .superTitle {
    font-size: 50px;
    opacity: 0.5;
    font-family: 'Arvo', serif;
}

.aboutPage .title {
    font-size: 80px;
    opacity: 1;
    font-family: 'Arvo', serif;
    margin-bottom: 25px;
}

.description {
    font-family: 'Roboto', sans-serif;
    line-height: 20px;
    margin-bottom: 15px;
}

.aboutPage .leftContent {
    margin: 225px 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 30%;
}

.aboutPage .rightContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 0;
}
.yearLineContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.year {
    background: linear-gradient(127.76deg, #0DFC9C 0%, #00BDC0 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: 1 !important;
    margin-bottom: 20px;
}

.line {
    box-sizing: border-box;
    width: 2px;
    border: 1px solid #FFFFFF;
    opacity: 0.5;
    margin-bottom: 20px;
}
.blockContainerLeft {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 140px;
}

@media (min-width: 2040px){
    .blockContainerLeft {
        padding-left: 10%;
    }
    .blockContainerRight {
        padding-right: 10%;
    }
    .block {
        width: 30% !important;
    }
}

.blockContainerRight {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 140px;
}

.block {
    display: flex;
    flex-direction: column;
    width: 40%;
}

.image {
    margin-bottom: 35px;
    width: 90px;
    border-radius: 50px;
}

.role {
    opacity: 0.5;
    font-size: 16px;
    margin-bottom: 12px;
}

.company {
    font-size: 40px;
    margin-bottom: 12px;
}

.timespan {
    font-size: 14px;
    font-family: Roboto, sans-serif;
    font-weight: bold;
    opacity: 0.5;
    margin-bottom: 30px;
}

.listContainer {
    padding-left: 15px !important;
}

.descList {
    font-size: 12px;
    font-weight: bold;
    font-family: Roboto, sans-serif;
    line-height: 20px;
}

.expPageContainer {
    color: white;
    height: 100%;
    position: relative;
    margin-top: 160px;
}

.timeline {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
}



.navbar {
    width: 100%;
    display: flex;
    height: 30px;
    justify-content: flex-end;
    position: relative;
}

.nameHome {
    background: linear-gradient(127.76deg, #0DFC9C 0%, #00BDC0 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: 1 !important;
    position: absolute;
    left: 0;
    margin-left: 0 !important;
}

.navbar a {
    margin-left: 45px;
    color: #FFFFFF;
    opacity: 0.5;
    font-size: 18px;
    cursor: pointer;
}

.navbar a:link {
    text-decoration: none;
}

.is-active {
    opacity: 1 !important;
}
.projectPageContainer {
    color: white;
    height: 100%;
}

.firstPage {
    min-height: 95vh;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
}

.secondPage {
    padding-top: 100px;
}

.projectPageContainer .leftContent {
    display: flex;
    flex-direction: column;
}

.projectSuperTitle {
    font-size: 14px;
    font-weight: bold;
    opacity: 0.5;
    font-family: Roboto, sans-serif;
    margin-bottom: 10px;
}

.overview {
    font-family: Arvo, serif;
    font-size: 40px;
    margin-bottom: 30px;
}

.projectPageContainer .description {
    font-size: 12px;
    font-weight: bold;
    font-family: Roboto, sans-serif;
    line-height: 20px;
    margin-bottom: 0;
    width: 300px;
}

.projectPageContainer .rightContent {
    position: relative;
    margin-right: 0;
}

.desktopPic {
    width: 600px;
}

.mobilePic {
    width: 210px;
    position: absolute;
    bottom: -40px;
    right: -95px;
    box-shadow: 1px 1px 10px #666666;
}

.imageContainer {
    padding-bottom: 200px;
}

